      // console.log("");
    }

    this.axios.get('/post').then((res)=>{
      if(res.data.code==1){
        // console.log('OK')
        // console.log(res.data.info)
        this.goods=res.data.info
        // console.log(this.goods)
      }else{
        // console.log('ERR')
      }
    })
  },
  watch: {
